@ccclass('OpInfo')
export class OpInfo extends BaseComponent {
    @property(Node)
    public opBtns: Node | null = null;

    @property(Node)
    public guoBtn: Node | null = null;

    @property(Node)
    public huBtn: Node | null = null;

    @property(Node)
    public gangBtn: Node | null = null;

    @property(Node)
    public pengBtn: Node | null = null;

    @property(Node)
    public chiBtn: Node | null = null;

    @property(Layout)
    public huLayout: Layout | null = null;

    @property(Layout)
    public zimoLayout: Layout | null = null;

    @property(Layout)
    public ganghuaLayout: Layout | null = null;

    //可以显示响应按钮的opCode
    btnCodeMap = new Map<number, Node | Layout>();

    /** 时间更新句柄 */
	_handlerTimeUpdate = null;

    protected onInitModuleEvent() {
		
	};

    onLoad () {
        this.updateCodeMap();
	};

    updateCodeMap(){
        this.btnCodeMap.set(OPCode.OPE_PASS, this.guoBtn);
        this.btnCodeMap.set(OPCode.OPE_RIGHT_CHI, this.chiBtn);
        this.btnCodeMap.set(OPCode.OPE_MIDDLE_CHI, this.chiBtn);
        this.btnCodeMap.set(OPCode.OPE_LEFT_CHI, this.chiBtn);
        this.btnCodeMap.set(OPCode.OPE_PENG, this.pengBtn);
        this.btnCodeMap.set(OPCode.OPE_QIANG_PENG, this.pengBtn);
        this.btnCodeMap.set(OPCode.OPE_HU, this.huLayout);
        this.btnCodeMap.set(OPCode.OPE_ZI_MO, this.zimoLayout);
        this.btnCodeMap.set(OPCode.OPE_AN_GANG, this.gangBtn);
        this.btnCodeMap.set(OPCode.OPE_GANG, this.gangBtn);
        this.btnCodeMap.set(OPCode.OPE_BU_GANG, this.gangBtn);
    }

	/** 开始更新时间 */
	startOperationTime() {
        this.stopSchedulerOnce(this._handlerTimeUpdate);
		this._handlerTimeUpdate = null;
        let timeOut:number = RoomMgr.getInstance().getTimeOut(GameStatus.OPERATION);
        this._handlerTimeUpdate = this.addSchedulerOnce(timeOut, Utils.handler(this, this.resetView))
	}


    //点击操作,发送操作code  碰/杠/胡/过
    opClick(opInfo:Common.IOpGroup) {
        let req: Game.ISendOperation = {
            opCard:OperationMgr.getInstance().getPassNumber(),
			opCode:opInfo.opCode,
			opCards:OperationMgr.getInstance().getOprationListNumber(opInfo.opCode),
			seatId:OperationMgr.getInstance().seatId,
			userId:OperationMgr.getInstance().userId
        } 
        EventManager.dispatch(GameEvent.SEND_PLAYER_OPERATION, req);
        OperationMgr.getInstance().myOpCode = opInfo.opCode;
        this.resetView();
    }

    getShowBtn = function (code: number): Node | null {
        for (let [opCode, btn] of this.btnCodeMap) {
            if (code == opCode) {
                return btn;
            }
        }
        return null;

    }

    

    updateOpration(resp:Game.IOperation){
        this.resetView();
        //抓牌
        if(resp.opGroups.length === 1){
            return;
        }
        console.log("updateOpration______________",resp.opGroups);
        
        resp.opGroups.forEach((opInfo:Common.IOpGroup) => {
            let btn = this.getShowBtn(opInfo.opCode);
            console.log("==this.btnCodeMap==", this.btnCodeMap, opInfo,btn)
            if (btn) {
                btn.active = true;
                console.log("==btn==", btn.active)
            }
            
            if (btn instanceof Layout) {
                btn.node.active = true;
                var card: Card = btn.node.getChildByName("Card").getComponent(Card);
                card.setCardByte(OperationMgr.getInstance().getPassNumber());
                btn = btn.node.getChildByName("hu");
            }
            if(btn){
                btn.off(Node.EventType.TOUCH_END);
                btn.on(Node.EventType.TOUCH_END, () => {
                    this.opClick(opInfo);
                }, this);
            }
            
        });

        this.startOperationTime();
    }

    resetView() {
        this.guoBtn.active = false;
        this.huLayout.node.active = false;
        this.zimoLayout.node.active = false;
        this.ganghuaLayout.node.active = false;
        this.gangBtn.active = false;
        this.pengBtn.active = false;
        this.chiBtn.active = false;
    }
}